"use client";

import { createContext, useContext, useState, useEffect, useMemo, ReactNode, useCallback } from 'react';
import * as propertyService from '../services/propertyService';

// Type definitions
export type CurrencyCode = 'USD' | 'EUR' | 'GBP' | 'INR' | 'AED' | 'CAD' | 'AUD' | 'JPY' | 'CNY' | 'SGD';
export type AreaUnit = 'sqft' | 'sqm' | 'acres' | 'hectares';
export type PropertyStatus =
    | 'online'
    | 'offline'
    | 'under_offer'
    | 'sold'
    | 'let'
    | 'draft'
    | 'published'
    | 'active'
    | 'available'
    | 'pending'
    | 'rented'
    | 'under_contract'
    | 'off_market'
    | 'coming_soon';
export type PropertyType = 'apartment' | 'house' | 'condo' | 'townhouse' | 'villa' | 'penthouse' | 'studio' | 'duplex' | 'triplex' | 'land' | 'commercial' | 'industrial' | 'office';
export type ListingType = 'sale' | 'rent' | 'lease' | 'short_term' | 'vacation';
export type FurnishingStatus = 'furnished' | 'semi_furnished' | 'unfurnished';
export type PropertyCondition = 'new' | 'excellent' | 'good' | 'fair' | 'needs_renovation';
export type FacingDirection = 'north' | 'south' | 'east' | 'west' | 'northeast' | 'northwest' | 'southeast' | 'southwest';

export interface PriceInfo {
    amount: number;
    currency: CurrencyCode;
    negotiable: boolean;
}

export interface Property {
    id: string;
    title: string;
    description?: string;
    shortDescription?: string;
    price?: PriceInfo;
    priceString?: string;
    propertyType: PropertyType;
    listingType: ListingType;
    status: PropertyStatus;

    // Location
    location?: {
        addressLine1?: string;
        addressLine2?: string;
        city?: string;
        cityId?: string;
        state?: string;
        stateId?: string;
        stateCode?: string;
        postalCode?: string;
        country?: string;
        countryCode?: string;
        countryId?: string;
        latitude?: number;
        longitude?: number;
        neighborhood?: string;
        landmark?: string;
    };
    address?: string;
    city?: string;
    state?: string;
    zipCode?: string;

    // Dimensions
    dimensions?: {
        totalArea: number;
        carpetArea?: number;
        areaUnit: AreaUnit;
        floors?: number;
        floorNumber?: number;
        totalFloors?: number;
    };
    area?: number;

    // Rooms
    rooms?: {
        bedrooms: number;
        bathrooms: number;
        balconies?: number;
        parkingSpaces?: number;
    };
    bedrooms?: number;
    bathrooms?: number;

    // Features
    yearBuilt?: number;
    furnishing: FurnishingStatus;
    condition: PropertyCondition;
    facing?: FacingDirection;
    amenities: {
        interior: string[];
        exterior: string[];
        community: string[];
        security: string[];
        utilities: string[];
    };
    features?: string[];

    // Media
    media?: {
        images: { id: string; url: string; type: string; isPrimary?: boolean; order?: number; uploadedAt: string }[];
        videos: { id: string; url: string; type: string; order?: number; uploadedAt: string }[];
        floorPlans: any[];
        virtualTourUrl?: string;
    };
    images?: (File | string)[];
    videos?: (File | string)[];
    virtualTourUrl?: string;

    // Contact
    contact?: {
        name: string;
        email: string;
        phone: string;
        alternatePhone?: string;
        preferredContactMethod: 'email' | 'phone' | 'whatsapp' | 'any';
        company?: string;
        licenseNumber?: string;
    };
    contactName?: string;
    phoneNumber?: string;
    emailAddress?: string;

    // Terms
    availableFrom: string;
    minimumLease?: number;
    inclusions?: string;
    exclusions?: string;
    financial?: {
        deposit?: number;
        maintenanceCharges?: number;
        maintenanceFrequency?: string;
    };

    // Analytics
    analytics: {
        views: number;
        inquiries: number;
        favorites: number;
        shares: number;
    };

    // Flags & Meta
    propertyId?: string;
    createdAt: string;
    updatedAt: string;
    published: boolean;
    draft: boolean;
    featured?: boolean;
    verified?: boolean;
    premium?: boolean;
    version?: number;
}

export interface PropertyFilters {
    search?: string;
    propertyType?: PropertyType[];
    listingType?: ListingType[];
    status?: (PropertyStatus | string)[];
    priceMin?: number;
    priceMax?: number;
    bedroomsMin?: number;
    bedroomsMax?: number;
    bathroomsMin?: number;
    areaMin?: number;
    areaMax?: number;
    city?: string;
    country?: string;
    furnishing?: FurnishingStatus[];
    featured?: boolean;
    verified?: boolean;
}

export type SortField = 'createdAt' | 'updatedAt' | 'price' | 'area' | 'bedrooms' | 'views' | 'title';
export type SortOrder = 'asc' | 'desc';

export interface SortOption {
    field: SortField;
    order: SortOrder;
}

export interface Pagination {
    page: number;
    limit: number;
    total: number;
    totalPages: number;
}

interface PropertyContextType {
    properties: Property[];
    filteredProperties: Property[];
    selectedProperties: string[];
    filters: PropertyFilters;
    sort: SortOption;
    pagination: Pagination;
    loading: boolean;
    error: string | null;

    fetchProperties: () => Promise<void>;
    addProperty: (property: Partial<Property>) => Promise<Property | null>;
    updateProperty: (id: string, property: Partial<Property>) => Promise<Property | null>;
    deleteProperty: (id: string) => Promise<void>;
    deleteProperties: (ids: string[]) => Promise<void>;
    duplicateProperty: (id: string) => Promise<Property | null>;
    getProperty: (id: string) => Property | undefined;
    uploadImages: (files: File[]) => Promise<string[]>;
    uploadVideos: (files: File[]) => Promise<string[]>;

    selectProperty: (id: string) => void;
    deselectProperty: (id: string) => void;
    selectAllProperties: () => void;
    clearSelection: () => void;
    bulkUpdateStatus: (ids: string[], status: PropertyStatus) => Promise<void>;

    setFilters: (filters: PropertyFilters) => void;
    clearFilters: () => void;
    setSort: (sort: SortOption) => void;
    setPage: (page: number) => void;
    setLimit: (limit: number) => void;

    incrementViews: (id: string) => void;
    incrementInquiries: (id: string) => void;
    incrementShares: (id: string) => void;
    exportProperties: (format: 'csv' | 'json' | 'pdf', ids?: string[]) => void;
    formatPrice: (price: PriceInfo | undefined) => string;
    formatArea: (area: number, unit: AreaUnit) => string;
    getPropertyStats: () => { total: number; available: number; sold: number; rented: number; pending: number };
}

const PropertyContext = createContext<PropertyContextType | undefined>(undefined);

export const useProperties = () => {
    const context = useContext(PropertyContext);
    if (!context) {
        throw new Error('useProperties must be used within PropertyProvider');
    }
    return context;
};

// Provider Implementation

export const PropertyProvider = ({ children }: { children: ReactNode }) => {
    const [properties, setProperties] = useState<Property[]>([]);
    const [selectedProperties, setSelectedProperties] = useState<string[]>([]);
    const [filters, setFiltersState] = useState<PropertyFilters>({});
    const [sort, setSort] = useState<SortOption>({ field: 'createdAt', order: 'desc' });
    const [pagination, setPagination] = useState<Pagination>({ page: 1, limit: 12, total: 0, totalPages: 1 });
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);

    const fetchProperties = async () => {
        setLoading(true);
        setError(null);
        try {
            const result = await propertyService.getProperties(filters);
            if (result.error) {
                setError(result.error);
                setProperties([]);
            } else if (result.data) {
                // Map backend properties if needed, but they should be compatible
                setProperties(result.data as any);
                setPagination(prev => ({ ...prev, total: result.data?.length || 0 }));
            }
        } catch (err: any) {
            setError(err.message);
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchProperties();
    }, [filters]);

    const filteredProperties = properties;
    // Placeholder for actual filtering logic

    return (
        <PropertyContext.Provider value={{
            properties,
            filteredProperties,
            selectedProperties,
            filters,
            sort,
            pagination,
            loading,
            error,
            fetchProperties,
            addProperty: async () => null,
            updateProperty: async () => null,
            deleteProperty: async () => { },
            deleteProperties: async () => { },
            duplicateProperty: async () => null,
            getProperty: (id) => properties.find(p => p.id === id),
            uploadImages: async () => [],
            uploadVideos: async () => [],
            selectProperty: (id) => setSelectedProperties(prev => [...prev, id]),
            deselectProperty: (id) => setSelectedProperties(prev => prev.filter(pId => pId !== id)),
            selectAllProperties: () => setSelectedProperties(properties.map(p => p.id)),
            clearSelection: () => setSelectedProperties([]),
            bulkUpdateStatus: async () => { },
            setFilters: setFiltersState,
            clearFilters: () => setFiltersState({}),
            setSort,
            setPage: (page) => setPagination(prev => ({ ...prev, page })),
            setLimit: (limit) => setPagination(prev => ({ ...prev, limit })),
            incrementViews: () => { },
            incrementInquiries: () => { },
            incrementShares: () => { },
            exportProperties: () => { },
            formatPrice: (price) => price ? new Intl.NumberFormat('en-US', { style: 'currency', currency: price.currency }).format(price.amount) : '',
            formatArea: (area, unit) => `${area} ${unit}`,
            getPropertyStats: () => ({ total: properties.length, available: 1, sold: 0, rented: 0, pending: 0 })
        }}>
            {children}
        </PropertyContext.Provider>
    );
};
